*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    /* background: linear-gradient(black white); */

}

/* navbar style start */
#nav{
    height: 80px;
    width: 100%;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    border-bottom: 1px solid black;
}
#nav>a{
    text-decoration: none;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-style: italic;
    text-shadow: 1px 2px 2px white;
}

/* Createuser style start */

#myForm{
    height: 80vh;
    width: 100%;
    /* background: linear-gradient(black rgb(146, 137, 137)); */
    display: flex;
    align-items: center;
    justify-content: center;
}
#myForm>form{
    height: 250px;
    width: 300px;
    border: 1px solid rgb(55, 52, 52);
    /* background: linear-gradient(black white); */
    color: rgb(26, 25, 25);
    font-style: italic;
    font-family: Arial, Helvetica, sans-serif;
    box-shadow:  0px 0px 5px rgb(69, 59, 59);
    border-radius: 10px;

}
