/* userPagr style start */

#userPage{
    height: 80vh;
    width: 100%;
    background: linear-gradient(black);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}
#Card{
    height: 200px;
    width: 200px;
    border: 1px solid rgb(17, 14, 14);
    color: rgb(36, 30, 30);
    font-style: italic;
    border-radius: 10px;
    box-shadow: 0px 0px 7px rgb(177, 156, 156);
    
}
#Card td{
